# 英语

## 时间

### 时钟

#### 整点

**基数词 + o'clock**，也可以省略·o'clock·
例如：·eight o'clock·：8点，·ten·：10点
特定：·noon·：中午12点，·midnight·：半夜0点

#### 时+分

**直接读数字**
例如：·six forty·：06:40
**分钟数少于等于30分钟用：分 + past/after + 时**，其中·past/after·是介词，意思是“过”
例如：·twenty past four·：04:20
**分钟数多于30分钟用：（60-分钟）+ to +（小时+1）**，其中·to·是介词，意思是“差”
例如：·twenty-five to nine·：08:35，差25分钟九点
**分钟数是30分钟可用：half（一半）**
例如：·half past nine·：09:30
**分钟数是15分钟可用：a quarter（一刻钟，四分之一）**
例如：·a quarter past twelve·：12:15

#### 上下午

表明上午在最后加上·am·或·a.m.·
例如：·thirteen past six am·：上午06:30
表明下午在最后加上·pm·或·p.m.·
例如：·four o'clock p.m.·：下午4点

#### 大概时间

在时间前加上介词·about·
例如：·about eight·：大约8点

#### 在某个时间点

在时间前面用介词·at·
例如：·at nine·：在9点钟，·at about five thirty-five pm·：大约在下午5点35分

### 单词本

